.App {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.map {
    z-index: 2;
    width: 100vw;
    height: 100vh;
}

.filterPanel {
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
/*	justify-content: center;
*/	-ms-flex-align: center;	align-items: center;
    position: absolute;
    z-index: 3;
    top: 120px;
    left: 10px;
    width: 100%;
    max-width: 300px;
    height: 50%;
    background: rgba(100, 100, 100, .85);
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    overflow-y: scroll;
}

.filterPanel input {
	white-space: normal;
	vertical-align: top;
    font-size: 14px;
    display: inline;
    width: 41%;
    height: 75px;
    margin: 10px;
    cursor: pointer;
    color: white;
    border: none;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.blueButton {
	background: rgb(20, 150, 250);
}

.redButton {
	background: rgb(204, 45, 50);
}

.purpleButton {
	background: rgb(160, 60, 140);
}

.greenButton {
	background: rgb(20, 180, 70);
}

.closePanel {
	top: 200%;
}

.filterPanel .selected {
	background: rgb(20, 180, 160);
}

.leaflet-container a.leaflet-popup-close-button {
	right: 20px;
	font: 40px/30px Tahoma, Verdana, sans-serif;
}

.filterInfo {
    position: absolute;
    z-index: 3;
    bottom: 5px;
    left: 50%;
	width: 280px;
    -webkit-transform: translate(-50%);
        -ms-transform: translate(-50%);
            transform: translate(-50%);
    color: white;
    background: rgba(86, 86, 86, .9);
    padding: 10px;
    border-radius: 1rem;
    font-size: 16px;
    text-align: center;
}


/*****BUTTONS*****/

.togglePanelButton {
	position: absolute;
	z-index: 3;
	width: 250px;
	height: 50px;
	left: 50px;
	top: 65px;
	cursor: pointer;
	color: white;
    border: none;
    background: rgb(90, 90, 90);
    font-size: 1em;
}

.geoLocateButton {
    position: absolute;
    z-index: 3;
    top: 10px;
    left: 50px;
    display: -ms-flexbox;
    display: flex;
    width: 250px;
	height: 50px;
    background: rgba(100, 100, 100, .8);
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border-radius: .3rem;
}

.geoLocateButton input {
    height: 100%;
    width: 100%;
    cursor: pointer;
    color: white;
    border: none;
    background: rgb(226, 70, 70);
}

.numberOfShelters {
    font-size: 1.0em;
    margin: 40px;
    color: white;
    text-align: center;
}

.externalLinkButton {
	position: absolute;
	z-index: 2;
}

.externalLinkButton a {
	text-decoration: none;
	color: #ecf0f1;;
	padding: 15px 30px;
	background: #8e44ad;
	font-size: 1em;
	border-radius: .3rem;
}

.volunteerSuppliesButton {
	bottom: 35px;
    right: -100%;
}

.addAShelterButton {
	bottom: 85px;
    right: -100%;
}

input {
	border-radius: .3rem;
}


/******Responsiveness*****/

@media (min-width: 600px) {
	.geoLocateButton {
		top: 10px;
		right: 20px;
		left: auto;
		height: 50px;
		width: 260px;
	}

	.geoLocateButton input {
	    font-size: 16px;
	}

	.togglePanelButton {
		top: 10px;
	}

	.filterPanel {
		-webkit-transform: translateY(0);
		    -ms-transform: translateY(0);
		        transform: translateY(0);
	    top: 85px;
	    bottom: auto;
	    height: 75vh;
	}
	.filterPanel input {
		display: block;
		font-size: 16px;
	    width: 250px;
	    height: 50px;
	}
	.numberOfShelters {
	    font-size: 1.3em;
	    margin: 20px;
	}
	.closePanel {
		top: 200%;
	}

	.filterPanel .selected {
		margin-left: 25px;
	}

	.volunteerSuppliesButton {
		bottom: 35px;
	    right: 10px;
	}

	.addAShelterButton {
		bottom: 85px;
	    right: 10px;
	}

	.filterInfo {
	    left: 10px;
	    -webkit-transform: translate(0);
	        -ms-transform: translate(0);
	            transform: translate(0);
	}
}


